#intro{
    height:81vh;
    width: 100vw;
    overflow: hidden;
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size:100vw;
}
.bg{
    position: absolute;
    top:5rem;
    left: 0;
    display: flex;
    object-fit: cover;
    height:68vh;
}
.introContent{
    top: 0;
    margin-left:16rem;
    height:50vh;
    width:80vw;
    padding:2rem;
    font-size:3.5rem;
    font-weight:800;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.boxpara{
    font-size:1.2rem;
    height: 3rem;
    top:0;
    display: flex;
    margin-left:33rem;
    align-items: center;
    justify-content: center;
    text-align: center;
    width:40vw;
    
}
.btn{
    margin:2rem;
    margin-left:40rem;
    font-weight:800;
    font-size:large;
    height:10vh;
    width:23vw;
    cursor: pointer;
    background:#FEE12B;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius:3rem;
}

.color-change {
    font-size: 3.5rem;
    color: transparent; /* Make text transparent */
    animation: colorChange 5s infinite; /* Apply the animation */
}

@keyframes colorChange {
    0% {
        color: red;
    }
    25% {
        color: blue;
    }
    50% {
        color: green;
    }
    75% {
        color: orange;
    }
    100% {
        color: red;
    }
}

