#slider{
    left: 0;
    height:15vh;
    background-color: white;
    margin-top:0rem;
    overflow: hidden;
    width:100%;
    max-width:90rem;
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.boxslider{
    width: 100%;
    max-width:90rem;
    margin-left:3rem;
    display: flex;
    margin:1rem;
}
.sliderImg{
  padding: 0 2rem;
    margin:1rem;
    display: inline-block;
    line-height: 100px;
}
.sliderTitle{
  text-align: center;
    margin-left:4rem;
    color: black;
    width:10rem;
}

.marquee {
  white-space: nowrap; /* Prevent line breaks */
  overflow: hidden; /* Hide overflowing content */
  animation: marquee 1s linear infinite; /* Adjust duration as needed */
}

.marquee img {
  margin-right: 20px; /* Adjust spacing between images */
}

@keyframes marquee {
  0% {
      transform: translateX(100%); /* Start off-screen to the right */
  }
  100% {
      transform: translateX(-100%); /* Move off-screen to the left */
  }
}