.navbar{
    opacity:90%;
    background-color: rgb(30, 30, 30);
    border-bottom: 0.5px solid white;
    height:4rem;
    width:87rem;
    max-width:87rem;
    margin:-2rem;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content:space-between ;
    position: sticky;
    top:0;
    z-index:3;
}
.logo{
    object-fit: cover;
    height:4rem;
    width:10rem;
}
.desktopMenuListItem{
    font-size:15px;
    margin:4rem;
    cursor: pointer;

}
.desktopMenuListItem:hover{
    color:#FEE12B;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #FEE12B;
}
.desktopMenuBtn{
    height: 2rem;
    cursor: pointer;
    background:#FEE12B;
    color: black;
    display: flex;
    border: none;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}
