#marketings{
    height:130vh;
    width: 100vw;
    overflow: hidden;
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size:100vw;
}
.marketingTitle{
    margin:3rem;
    font-size:2rem;
    font-weight:900;
    text-align: center;
    align-items: center;
    flex-direction:column;
    justify-content: center;
}
#mainBlock{
    margin-left:4rem;
    display: flex;
    flex-wrap: wrap;
}
.serviceBlocks{
    padding:1rem;
    width:28vw;
    text-align:left;
}
.serviceBlock{
    display: flex;
    margin: 0.2rem;
    padding: 1rem 1rem;
    border-radius:6rem;
    background:rgb(40, 40, 40);
    opacity:75%;
}
.serviceBlockImg{
    margin-top:0.4rem;
    object-fit: cover;
    height: 4rem;
    width: 4rem;
    margin-left:1rem;
}
.serviceBlockText{
    margin-left:1rem;
    padding:1rem;
}